<template>
	<div>
		<button v-if="isShow" id="butConnect" class="next buttonX" style="margin-top: 20px;" @click="clickConnect()">{{content}}</button>
		<p>UV: {{uv}}</p>
		<p>LUX: {{lux}}</p>
	</div>
</template>

<script>
  	let reader;
	export default {
		data() {
			return {
        		port: null,
        		inputDone: null,
        		isShow: true,
        		content: 'UV Connect', 
        		uv: null,
        		lux: null,
			}
		},
		created() {
			if (sessionStorage.connection == 'true') {
				this.readLoop()
				.catch((e) => {
					console.log(e)
  					// this.$store.dispatch('setConnectionData', false)
  				});
			}
		},
		methods: {
			async connect() {
  				this.port = await navigator.serial.requestPort();
  				await this.port.open({ baudrate: 230400 });
  				let decoder = new TextDecoderStream();
          		reader = this.port.readable.getReader();
  				this.readLoop()
  				.catch((e) => {
  					console.log(e)
  					// this.$store.dispatch('setConnectionData', false)
  				});
			},
			async disconnect() {
				if (reader) {
					await reader.cancel();
					reader = null;
				}
				await this.port.close();
				this.port = null;
			},
			async clickConnect() {
				if (this.port) {
					await this.disconnect();
					this.toggleUIConnected(false);
					return;
				}
  				await this.connect();
  				this.toggleUIConnected(true);
			},
			async readLoop() {
				let count = 0;
				// this.$store.dispatch('setConnectionData', true)
  				while (true) {
    				const { value, done } = await reader.read();
    				this.$emit('nsensor_connected', true);
            		count++;
    				if (value.length == 10) {
						const unit_uv = value.slice(1,5);
						const unit_lux = value.slice(6);
						const hex_uv = '0x' + this.buf2hex(unit_uv.buffer);
						const hex_lux = '0x' + this.buf2hex(unit_lux.buffer);
						const u = new DataView(new ArrayBuffer(4));
						const l = new DataView(new ArrayBuffer(4));
						u.setUint32(0, hex_uv);
						l.setUint32(0, hex_lux);
						const uv = u.getFloat32(0);
						const lux = l.getFloat32(0);
						// this.isShow = false;
						this.uv =  uv.toFixed(1);
						this.lux =  lux.toFixed(1);
						// this.$emit('ncount', count);
					}
					if (done) {
						console.log("[readLoop] DONE", done);
						reader.releaseLock();
						break;
					}
				}
			},
			toggleUIConnected(connected) {
				const butConnect = document.getElementById("butConnect");
  				if (connected) {
    				this.content = "UV Disconnect";
  				} else {
  					this.content = 'UV Connect'
  				}
			},
			buf2hex(buffer) {
				return Array.prototype.map.call(new Uint8Array(buffer), x => ('00' + x.toString(16)).slice(-2)).join('');
			}
		}
	}
</script>

<style scoped>
button {
	width: 144px;
      height: 40px;
      border-radius: 5px;
      font-size: 18px;
      font-weight: 600;
      color: white;
      background-color: #FA5708;
      border: 2px solid #FDB18B;
      cursor: pointer;
}
</style>