<template>
	<div>
		<button v-if="isShow" id="butConnect" class="next buttonX" style="margin-top: 20px;" @click="clickConnect()">{{content}}</button>
		<p>Temperature: {{temperature}}℃</p>
		<p>Humidity: {{humidity}}%</p>
	</div>
</template>

<script>
  	let reader;
	export default {
		data() {
			return {
        		port: null,
        		inputDone: null,
        		isShow: true,
        		raw: null,
        		temperature: null,
        		humidity: null,
        		content: 'Humidity Connect',
			}
		},
		created() {
			if (sessionStorage.connection == 'true') {
				this.readLoop()
				.catch((e) => {
					console.log(e)
  					this.$store.dispatch('setConnectionData', false)
  				});
			}
		},
		methods: {
			async connect() {
  				this.port = await navigator.serial.requestPort({filters: [{usbVendorId: 0x04D8}]});
  				await this.port.open({ baudrate: 230400 });
  				// this.port.getInfo()
  				let decoder = new TextDecoderStream();
          		reader = this.port.readable.getReader();
  				this.readLoop()
  				.catch((e) => {
  					console.log(e)
  					// this.$store.dispatch('setConnectionData', false)
  				});
			},
			async disconnect() {
				if (reader) {
					await reader.cancel();
					reader = null;
				}
				await this.port.close();
				this.port = null;
			},
			async clickConnect() {
				if (this.port) {
					await this.disconnect();
					this.toggleUIConnected(false);
					this.contet = "Humidity Connect";
					console.log('s')
					return;
				}
  				await this.connect();
  				this.contet = "Humidity Disconnect";
  				this.toggleUIConnected(true);
			},
			async readLoop() {
				let count = 0;
				let flag = 0
				let t_temp = []
				let t_hum = []
				let temp, hum
  				while (true) {
    				const { value, done } = await reader.read();
    				
    				console.log(value)
    				if (value.length == 24) {
    					let temp_a = value[5].toString(16)
    					let temp_b = value[4].toString(16)
    					let hum_a = value[7].toString(16)
    					let hum_b = value[6].toString(16)
    					if (temp_b < 10) {
    						temp_b = '0' + temp_b
    					}
    					if (hum_b < 10) {
    						hum_b = '0' + hum_b
    					}
    					temp ='0x' + temp_a + temp_b
    					hum ='0x' + hum_a + hum_b
					}
					if (flag < 10) {
						t_temp.push(parseInt(temp)/100)
						t_hum.push(parseInt(hum)/100)
						flag ++
					} else {
						t_temp.shift()
						t_temp.push(parseInt(temp)/100)
						t_hum.shift()
						t_hum.push(parseInt(hum)/100)
						const sum_temp = t_temp.reduce((previous, current) => current += previous)
						const sum_hum = t_hum.reduce((previous, current) => current += previous)
						this.temperature = (sum_temp/5).toFixed(1)
						this.humidity = (sum_hum/5).toFixed(1)
					}

            		count++;
					if (done) {
						console.log("[readLoop] DONE", done);
						reader.releaseLock();
						break;
					}
				}
			},
			toggleUIConnected(connected) {
				const butConnect = document.getElementById("butConnect");
  				if (connected) {
    				this.content = "Humidity Disconnect";
  				} else {
  					this.content = 'Humidity Connect'
  				}
			},
			buf2hex(buffer) {
				return Array.prototype.map.call(new Uint8Array(buffer), x => ('00' + x.toString(16)).slice(-2)).join('');
			}
		}
	}
</script>

<style scoped>
button {
	width: 144px;
      height: 40px;
      border-radius: 5px;
      font-size: 18px;
      font-weight: 600;
      color: white;
      background-color: #FA5708;
      border: 2px solid #FDB18B;
      cursor: pointer;
}
</style>