<template>
	<div>
		<button v-if="isShow" id="butConnect" class="next buttonX" style="margin-top: 20px;" @click="clickConnect()">{{content}}</button>
		<p>Red: {{r}}</p>
		<p>Green: {{g}}</p>
		<p>Blue: {{b}}</p>
		<p>White: {{w}}</p>
	</div>
</template>

<script>
  	let reader;
	export default {
		data() {
			return {
        		port: null,
        		inputDone: null,
        		isShow: true,
        		raw: null,
        		r: null,
        		g: null,
        		b: null,
        		w: null,
        		content: 'Color Connect'
			}
		},
		created() {
			if (sessionStorage.connection == 'true') {
				this.readLoop()
				.catch((e) => {
					console.log(e)
  					this.$store.dispatch('setConnectionData', false)
  				});
			}
		},
		methods: {
			async connect() {
  				this.port = await navigator.serial.requestPort();
  				await this.port.open({ baudrate: 230400 });
  				// this.port.getInfo()
  				let decoder = new TextDecoderStream();
          		reader = this.port.readable.getReader();
  				this.readLoop()
  				.catch((e) => {
  					console.log(e)
  					// this.$store.dispatch('setConnectionData', false)
  				});
			},
			async disconnect() {
				if (reader) {
					await reader.cancel();
					reader = null;
				}
				await this.port.close();
				this.port = null;
			},
			async clickConnect() {
				if (this.port) {
					await this.disconnect();
					this.toggleUIConnected(false);
					return;
				}
  				await this.connect();
  				this.toggleUIConnected(true);
			},
			async readLoop() {
				let count = 0;
  				while (true) {
    				const { value, done } = await reader.read({filters: [{usbVendorId: 0x04D8}]});
    				
    				console.log(value)
    				if (value.length == 24) {
    				let data0_a = value[5].toString(16)
    				let data0_b = value[4].toString(16)
    				let data1_a = value[7].toString(16)
    				let data1_b = value[6].toString(16)
    				let data2_a = value[9].toString(16)
    				let data2_b = value[8].toString(16)
    				let data3_a = value[11].toString(16)
    				let data3_b = value[10].toString(16)
    				if (data0_b < 10) {
    					data0_b = '0' + data0_b
    				}
    				if (data1_b < 10) {
    					data1_b = '0' + data1_b
    				}
    				if (data2_b < 10) {
    					data2_b = '0' + data2_b
    				}
    				if (data3_b < 10) {
    					data3_b = '0' + data3_b
    				}
    				const data0 ='0x' + data0_a + data0_b
    				const data1 ='0x' + data1_a + data1_b
    				const data2 ='0x' + data2_a + data2_b
    				const data3 ='0x' + data3_a + data3_b
    				this.r = parseInt(data0)
    				this.g = parseInt(data1)
    				this.b = parseInt(data2)
    				this.w = parseInt(data3)
					}
            		count++;
					if (done) {
						console.log("[readLoop] DONE", done);
						reader.releaseLock();
						break;
					}
				}
			},
			toggleUIConnected(connected) {
				const butConnect = document.getElementById("butConnect");
  				if (connected) {
    				this.content = "Color Disconnect";
  				} else {
  					this.content = 'Color Connect'
  				}
			},
			buf2hex(buffer) {
				return Array.prototype.map.call(new Uint8Array(buffer), x => ('00' + x.toString(16)).slice(-2)).join('');
			}
		}
	}
</script>

<style scoped>
button {
	width: 144px;
      height: 40px;
      border-radius: 5px;
      font-size: 18px;
      font-weight: 600;
      color: white;
      background-color: #FA5708;
      border: 2px solid #FDB18B;
      cursor: pointer;
}
</style>